// import * as d3 from 'd3';

import {select, selectAll} from 'd3-selection';
import {interval, timeout} from 'd3-timer';
import {transition} from 'd3-transition';

import {getTranslation} from '../scripts/getTranslation';

const d3 = Object.assign(
	{},
	{
		select,
		selectAll,
		interval,
		transition,
		timeout,
	},
);

let flagSvgInvert = true;
let timeOut;
let timeInter;
window.startPromotionAnime = () => {
	const pinBefore = d3.select('.js-promotion-svg .pin-before');
	const pinBeforePath = d3.select('.js-promotion-svg .pin-before path');
	const pins = d3.selectAll('.js-promotion-svg .pin-next');

	const trfmBefore = getTranslation(pinBefore.attr('transform'));
	pinBefore
		.attr('transform', `translate(${trfmBefore[0]}, ${trfmBefore[1] - 50})`)
		.transition()
		.delay(700)
		.duration(200)
		.attr('opacity', 1)
		.attr('transform', `translate(${trfmBefore[0]}, ${trfmBefore[1]})`);
	pinBeforePath
		.attr('transform', 'rotate(0 21 56)')
		.transition()
		.delay(900)
		.duration(100)
		.attr('transform', 'rotate(-7 21 56)')
		.transition()
		.delay(100)
		.duration(100)
		.attr('transform', 'rotate(7 21 56)')
		.transition()
		.delay(100)
		.duration(100)
		.attr('transform', 'rotate(0 21 56)');

	timeOut = d3.timeout(() => {
		let i = 0;
		timeInter = d3.interval(function() {
			const pin = d3.select(pins.nodes()[i]);
			const trfm = getTranslation(pin.attr('transform'));

			pin
				.attr('transform', `translate(${trfm[0]}, ${trfm[1] - 50})`)
				.transition()
				.duration(200)
				.attr('opacity', 1)
				.attr('transform', `translate(${trfm[0]}, ${trfm[1]})`);

			i++;
			if (i > 8) {
				timeOut.stop();
				timeInter.stop();
			}
		}, 300);
	}, 4000);

	const btn = d3.select('.js-promotion-svg .svg-button');
	// Появление кнопки
	btn
		.transition()
		.duration(300)
		.attr('opacity', 1);

	const btnText = d3.select('.js-promotion-svg .svg-button text');
	// Появления текста
	btnText
		.attr('style', 'font-size: 54px')
		.transition()
		.delay(300)
		.duration(300)
		.attr('opacity', 1)
		.attr('style', 'font-size: 27px')
		.transition()
		.delay(1000)
		.duration(0)
		.attr('opacity', 0)
		.attr('style', 'font-size: 54px')
		.text('После')
		.transition()
		.delay(900)
		.duration(300)
		.attr('opacity', 1)
		.attr('style', 'font-size: 27px');

	const svgTextHeader = d3.select('.js-header-promotion');
	svgTextHeader
		.attr('style', 'opacity: 1')
		.transition()
		.delay(6500)
		.duration(300)
		.attr('style', 'opacity: 0')
		.transition()
		.delay(20)
		.duration(0)
		.transition()
		.delay(20)
		.duration(300)
		.attr('style', 'opacity: 1');
	setTimeout(() => {
		svgTextHeader.html(
			'Сайт в топе поисковых систем<br /><span>в нужных регионах страны</span>',
		);
	}, 6900);

	let btnClickAllowed = false;
	setTimeout(() => {
		btnClickAllowed = true;
		flagSvgInvert = true;
	}, 7000);
	let allowAnimation = true;
	btn.on('click', function() {
		if (!btnClickAllowed) {
			return false;
		}
		if (allowAnimation) {
			allowAnimation = false;
			// const text = d3
			// 	.select(this)
			// 	.text()
			// 	.replace(/\s+/g, '');

			svgTextHeader
				.attr('style', 'opacity: 1')
				.transition()
				.delay(800)
				.duration(300)
				.attr('style', 'opacity: 0')
				.transition()
				.delay(20)
				.duration(0)
				.transition()
				.delay(20)
				.duration(300)
				.attr('style', 'opacity: 1');

			if (flagSvgInvert) {
				flagSvgInvert = false;
				btnText
					.transition()
					.duration(200)
					.attr('style', 'font-size: 54px')
					.attr('opacity', 0)
					.transition()
					.delay(200)
					.text('До')
					.transition()
					.duration(200)
					.attr('opacity', 1)
					.attr('style', 'font-size: 27px');
				pins
					.transition()
					.delay(600)
					.duration(200)
					.attr('opacity', 0);
				setTimeout(() => {
					svgTextHeader.html(
						'98% посетителей сайта местные <br /><span>и 2% случайные из других регионов</span>',
					);
				}, 1100);
			} else {
				flagSvgInvert = true;
				btnText
					.transition()
					.duration(200)
					.attr('style', 'font-size: 54px')
					.attr('opacity', 0)
					.transition()
					.delay(200)
					.text('После')
					.transition()
					.duration(200)
					.attr('opacity', 1)
					.attr('style', 'font-size: 27px');
				pins
					.transition()
					.delay(600)
					.duration(200)
					.attr('opacity', 1);
				setTimeout(() => {
					svgTextHeader.html(
						'Сайт в топе поисковых систем<br /><span>в нужных регионах страны</span>',
					);
				}, 1100);
			}
			setTimeout(() => {
				allowAnimation = true;
			}, 1400);
		}
	});
};

window.stopPromotionAnime = () => {
	if (timeOut && timeInter) {
		timeOut.stop();
		timeInter.stop();
	} else {
		return false;
	}
	setTimeout(() => {
		flagSvgInvert = false;
		d3.select('.js-promotion-svg .pin-before').attr('opacity', 0);
		d3.select('.js-promotion-svg .svg-button').attr('opacity', 0);
		d3.select('.js-header-promotion').html(
			'98% посетителей сайта местные <br /><span>и 2% случайные из других регионов</span>',
		);
		d3.select('.js-promotion-svg .svg-button text').text('До');
		d3.selectAll('.js-promotion-svg .pin-next').attr('opacity', 0);
	}, 300);
};



