// import * as d3 from 'd3';
import {select, selectAll} from 'd3-selection';
import {transition} from 'd3-transition';

import PerfectScrollbar from 'perfect-scrollbar';

import {cities} from './map-list';
import scrollTo from './scrollTo';

const d3 = Object.assign(
	{},
	{
		select,
		selectAll,
		transition,
	},
);

window.initBlockGeography = () => {
	const svg = d3.select('.svg-map svg');
	const pathMap = svg.select('#path-map');
	const pathLines = svg.select('#pathLine');
	const circle = svg.select('circle');
	const circles = svg.selectAll('circle');
	const popup = d3.select('.js-svg-popup');

	const pathMapLength = pathMap.node().getTotalLength();
	const pathLinesLength = pathMap.node().getTotalLength();
	const circleLength = circle.node().getTotalLength();

	pathMap
		.attr('stroke', '#F0F0F0')
		.attr('stroke-dasharray', pathMapLength + ' ' + pathMapLength)
		.attr('stroke-dashoffset', pathMapLength)
		.transition()
		.duration(1500)
		.attr('stroke-dashoffset', 0);

	pathLines
		.attr('stroke', '#FFD524')
		.attr('stroke-dasharray', pathLinesLength + ' ' + pathLinesLength)
		.attr('stroke-dashoffset', pathLinesLength)
		.transition()
		.delay(2000)
		.duration(3000)
		.attr('stroke-dashoffset', 0);

	circles
		.attr('stroke', '#FFD524')
		.attr('data-city', function(d, i) {
			return cities[i].city;
		})
		.attr('stroke-dasharray', function(d, i) {
			return i === 0
				? `${(circleLength / 100) * 11.2 + circleLength} ${(circleLength /
						100) *
						11.2 +
						circleLength}`
				: `${circleLength} ${circleLength}`;
		})
		.attr('stroke-dashoffset', (d, i) =>
			i === 0 ? (circleLength / 100) * 11.2 + circleLength : circleLength,
		)
		.attr('fill', 'none')
		.transition()
		.delay(1500)
		.duration(500)
		.attr('fill', '#171717')
		.duration(1000)
		.attr('stroke-dashoffset', 0);

	// winit();

	const newCoordinate = x => {
		const svgWidth = document
			.querySelector('.js-geography-svg')
			.getBoundingClientRect().width;
		const pricent = (svgWidth * 100) / 900;
		return (x * pricent) / 100;
	};

	circles.on('mouseover', function() {
		circles
			.transition()
			.duration(300)
			.attr('r', '3.93');
		const elem = d3.select(this);
		elem
			.attr('stroke-dasharray', '100 100')
			.attr('stroke-dashoffset', 0)
			.transition()
			.duration(300)
			.attr('r', '6.44');
		const cx = newCoordinate(elem.attr('cx'));
		const cy = newCoordinate(elem.attr('cy'));
		const id = elem.attr('data-city-id');
		const city = elem.attr('data-city');

		popup.attr('data-city-id', id).html(city);

		const offsetLeft = parseInt(popup.style('width'), 10) / 2;
		const offsetTop = parseInt(popup.style('height'), 10);
		popup
			.style('top', cy - offsetTop - 20 + 'px')
			.style('left', cx - offsetLeft + 'px')
			.transition()
			.delay(1)
			.duration(300)
			.style('opacity', 1);
		circles.classed('active', false);
	});

	circles.on('mouseout', function() {
		const elem = d3.select(this);
		if (!elem.classed('active')) {
			elem
				.attr('stroke-dasharray', '100 100')
				.attr('stroke-dashoffset', 0)
				.transition()
				.duration(300)
				.attr('r', '3.93');
			popup
				.transition()
				.duration(300)
				.style('opacity', 0);
		}
	});

	circles.on('click', function(id) {
		let elem;
		if (typeof id !== 'undefined') {
			circles
				.attr('stroke-dasharray', '100 100')
				.attr('stroke-dashoffset', 0)
				.transition()
				.duration(300)
				.attr('r', '3.93');
			elem = svg.select(`circle[data-city-id="${id}"]`);
		} else {
			elem = d3.select(this);
			elem.classed('active', true);
			d3.selectAll('.js-map-list li').on('add-to-active')(
				parseInt(elem.attr('data-city-id'), 10),
			);
		}
		elem
			.attr('stroke-dasharray', '100 100')
			.attr('stroke-dashoffset', 0)
			.transition()
			.duration(300)
			.attr('r', '6.44');
		const cx = newCoordinate(elem.attr('cx'));
		const cy = newCoordinate(elem.attr('cy'));
		const city = elem.attr('data-city');
		popup.attr('data-city-id', id).html(city);
		const offsetLeft = parseInt(popup.style('width'), 10) / 2;
		const offsetTop = parseInt(popup.style('height'), 10);

		popup
			.style('top', cy - offsetTop - 20 + 'px')
			.style('left', cx - offsetLeft + 'px')
			.transition()
			.delay(1)
			.duration(300)
			.style('opacity', 1);
	});

	d3.selectAll('.js-map-list li').on('click', function(elem) {
		d3.selectAll('.js-map-list li').classed('active', false);
		const li = d3.select(this ? this : elem);
		const id = li.attr('data-city-id');
		svg.select(`circle[data-city-id="${id}"]`).on('click')(id);
		li.classed('active', true);
	});

	let initListEvents = false;
	setTimeout(() => {
		d3.select('.js-map-list li:first-of-type').on('click')(
			'.js-map-list li:first-of-type',
		);
		initListEvents = true;
	}, 3500);

	let scrollTrue = true;
	const scrollList = (id, scrollTime, toScroll) => {
		d3.select(`.js-map-list li[data-city-id="${id}"]`).on('click')(
			`.js-map-list li[data-city-id="${id}"]`,
		);
		scrollTo(document.querySelector('.js-map-list'), toScroll, scrollTime);
		setTimeout(() => {
			scrollTrue = true;
		}, scrollTime);
	};

	d3.selectAll('.js-map-list li').on('add-to-active', function(id) {
		if (scrollTrue && initListEvents) {
			scrollTrue = false;
			const scrollTime = 500;
			d3.selectAll('.js-map-list li').classed('active', false);
			const li = d3.select(`.js-map-list li[data-city-id="${id}"]`);
			li.classed('active', true);
			const toScroll =
				(id - 1) *
				parseInt(
					d3
						.select('.js-map-list li')
						.node()
						.getBoundingClientRect().height,
					10,
				);
			scrollList(id, scrollTime, toScroll);
		}
	});

	d3.select('.js-svg-button-down').on('click', function() {
		if (scrollTrue && initListEvents) {
			scrollTrue = false;
			const scrollTime = 500;
			const $liActive = d3.select('.js-map-list li.active');
			const id = parseInt($liActive.attr('data-city-id'), 10) + 1;
			const toScroll =
				(id - 1) *
				parseInt(
					d3
						.select('.js-map-list li')
						.node()
						.getBoundingClientRect().height,
					10,
				);
			if (d3.select(`.js-map-list li[data-city-id="${id}"]`).size()) {
				scrollList(id, scrollTime, toScroll);
			} else {
				scrollList(0, scrollTime, 0);
			}
		}
	});

	d3.select('.js-svg-button-up').on('click', function() {
		if (scrollTrue && initListEvents) {
			scrollTrue = false;
			const scrollTime = 500;
			const $liActive = d3.select('.js-map-list li.active');
			const id = parseInt($liActive.attr('data-city-id'), 10) - 1;
			const toScroll =
				(id - 1) *
				parseInt(
					d3
						.select('.js-map-list li')
						.node()
						.getBoundingClientRect().height,
					10,
				);
			if (d3.select(`.js-map-list li[data-city-id="${id}"]`).size()) {
				scrollList(id, scrollTime, toScroll);
			} else {
				const len = parseInt(d3.selectAll('.js-map-list li').size(), 10) - 1;
				scrollList(len, scrollTime, toScroll * len * -1);
			}
		}
	});
};

if (document.querySelector('.js-map-list')) {
	new PerfectScrollbar('.js-map-list'); // eslint-disable-line
}



